  float: left;
  margin-right: 1rem;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1rem;
}

.aligncenter {
  display: block;
}

.entry-footer span {
  padding-right: 10px;
}

img.wp-post-image,
article img,
figure,
img,
#secondary img {
  max-width: 100%;
  height: auto;
}

a.skip-link {
  position: fixed;
  z-index: 1000;
  top: 0px;
  right: 0px;
}

.jumbotron {
  margin-bottom: 0px;
}

.navbar-dark .navbar-nav .dropdown-menu .nav-link {
  display: block;
  width: 100%;
  padding: 0.5rem 1rem;
  clear: both;
  font-weight: 400;
  color: #1a1a1a !important;
  text-align: inherit;
  white-space: nowrap;
  background: none;
  border: 0;
}
.navbar-dark .navbar-nav .dropdown-menu .nav-link:hover, .navbar-dark .navbar-nav .dropdown-menu .nav-link:focus {
  color: #0d0d0d !important;
  text-decoration: none;
  background-color: #e6e6e6;
}
.navbar-dark .navbar-nav .dropdown-menu .nav-link.active, .navbar-dark .navbar-nav .dropdown-menu .nav-link:active {
  color: #fff !important;
  text-decoration: none;
  background-color: #625f9d;
}
