.sr-only, .screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}
.gallery-columns-2 .gallery-item {
  max-width: 50%;
}
.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}
.gallery-columns-4 .gallery-item {
  max-width: 25%;
}
.gallery-columns-5 .gallery-item {
  max-width: 20%;
}
.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}
.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}
.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}
.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}

html {
  scroll-behavior: smooth;
}

/* Fixed header fix */
:target:before {
  content: "";
  display: block;
  height: 65px;
  /* same as your fixed header height */
  margin: -65px 0 0;
  /* negative fixed header height */
}

.admin-bar .fixed-top {
  top: 46px;
}
@media (min-width: 782px) {
  .admin-bar .fixed-top {
    top: 32px;
  }
}

.full-screen {
  min-height: 100vh;
}

.pagination {
  margin-top: 2rem;
  justify-content: center;
}

.comments-area {
  margin-top: 3rem;
}

.back-to-top {
  position: fixed;
  right: 1rem;
  bottom: -60px;
  width: 60px;
  height: 60px;
  background-color: #625f9d;
  border: 1px solid transparent;
  color: #fff;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  z-index: 10000;
  transform: rotate(270deg);
  background-image: url("../img/flecha-derecha-blanca.svg");
  background-size: 30px auto;
  background-position: center;
  background-repeat: no-repeat;
}
.back-to-top:hover {
  background-color: #4e4c7d;
  border: 1px solid #4e4c7d;
}

.scrolled .back-to-top {
  opacity: 0.9;
  bottom: 1rem;
}

.mw-300 {
  max-width: 300px;
}

.mw-400 {
  max-width: 400px;
}

.mw-500 {
  max-width: 500px;
}

.mw-600 {
  max-width: 600px;
}

.mw-700 {
  max-width: 700px;
}

.mw-800 {
  max-width: 800px;
}

.mt-neg-200 {
  margin-top: -200px;
  position: relative;
  z-index: 1;
}

#search-wrapper .page-header,
#error-404-wrapper .page-header {
  margin-bottom: 2rem;
}

.stretch-linked-block {
  position: relative !important;
  transition: opacity 0.15s linear;
}
.stretch-linked-block .editorskit-block-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 90;
}
.stretch-linked-block:hover {
  opacity: 0.7;
}
.stretch-linked-block:hover .wp-block-button.is-style-arrow-link .wp-block-button__link:after {
  right: 0;
}

.stretched-link {
  color: #1a1a1a;
}
.stretched-link:hover {
  text-decoration: none;
  color: #1a1a1a;
}

.entry-footer {
  position: relative;
  z-index: 2;
}

