@media (max-width: 781.98px) {
  #wrapper-footer {
    text-align: center;
  }
}

.timeline-block {
  position: relative;
}
.timeline-block:before {
  content: "";
  position: absolute;
  left: 1rem;
  top: 0;
  bottom: 0;
  width: 1px;
  background-color: #625f9d;
}

.timeline-item {
  position: relative;
  padding-left: 3rem;
  display: flex;
  gap: 16px;
  margin-bottom: 2rem;
}
.timeline-item.has-not-image .timeline-item-title {
  position: relative;
}
.timeline-item.has-not-image .timeline-item-title:after {
  content: "";
  width: 2rem;
  height: 2rem;
  border-radius: 2rem;
  background-color: #cdcdcd;
  position: absolute;
  left: -3rem;
  top: 0;
}
.timeline-item .timeline-item-image-wrapper {
  position: relative;
}
.timeline-item .timeline-item-image-wrapper:before {
  content: "";
  position: absolute;
  left: -2rem;
  top: 1rem;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
  z-index: -1;
}
.timeline-item.has-image .timeline-item-image-wrapper:before {
  top: 2rem;
}
@media (min-width: 782px) {
  .timeline-item.has-image .timeline-item-image-wrapper:before {
    top: 4.5rem;
  }
}
.timeline-item.timeline-item-featured .timeline-item-title:after {
  background-color: #625f9d;
}
.timeline-item.timeline-item-featured .timeline-item-content {
  color: #625f9d;
}

.timeline-item-image-wrapper {
  flex: 0 0 4rem;
}
@media (min-width: 782px) {
  .timeline-item-image-wrapper {
    flex: 0 0 9rem;
  }
}
.timeline-item-image-wrapper img {
  border: 0.25rem solid #fff;
}

.timeline-item-title {
  color: #625f9d;
}

@media (min-width: 1280px) {
  .timeline-block {
    margin-left: calc(50% - 1rem);
  }

  .timeline-item {
    min-height: 6rem;
  }
  .timeline-item:not(:last-child) {
    margin-bottom: -1rem;
  }
  .timeline-item:nth-child(even) {
    flex-direction: row-reverse;
    text-align: right;
    padding-left: 0;
    padding-right: 3rem;
    right: calc(100% - 2rem);
  }
  .timeline-item:nth-child(even).has-not-image .timeline-item-title:after {
    left: unset;
    right: -3rem;
  }
  .timeline-item:nth-child(even) .timeline-item-image-wrapper:before {
    left: unset;
    right: -2rem;
  }
}
